<template>
    <v-container>
        <LevelOne
            v-if="currentLvl==1"
        />
        <LevelTwo
            v-if="currentLvl==2"
        />
        <LevelThree
            v-if="currentLvl==3"
        />
    </v-container>
</template>

<script>
import LevelOne from '@/components/Level1.vue';
import LevelTwo from '@/components/Level2.vue';
import LevelThree from '@/components/Level3.vue';

export default {
    name: 'level',
    data: () => ({
        currentLvl : 0,
    }),
    components :{
        LevelOne,
        LevelTwo,
        LevelThree,
    },
    created(){
        this.currentLvl = this.$route.params.level;
    }
}
</script>

