<template>
    <div
        id="no-select"
    >
      <v-row>
      <v-col
        md="auto"
      >
        <v-btn
          color="indigo lighten-4"
          outlined
          @click="toMenu"
        >К уровням</v-btn>
      </v-col>
      <v-col>
        <div :id="containerId" v-if="downloaded" />
        <div class="placeholder" v-else>
            Downloading ...
        </div>
      </v-col>
      </v-row>
      <div class="modal-backdrop"
            v-if="fin"
      >
        <div class="modal">
          <header class="modal-header">
            <div
              v-if="status=='success'"
            >Ого, вот это результат!</div>
            <div
              v-else
            >Ты проиграл, попробуй ещё разок!</div>
          </header>
          <section class="modal-body">
              <slot name="body">
                <v-img
                  v-if="status=='success'"
                  src='../../public/assets/lvl2/success_fin.png'
                  contain   
                  height="300"
                  width="450"
                  
                ></v-img>
                <v-img
                  v-else
                  src='../../public/assets/lvl2/unsuccess_fin.png'
                  contain   
                  height="300"
                  width="450"
                ></v-img>
                  <v-btn
                      block
                      @click="toMenu"
                  >К уровням</v-btn>
              </slot>
          </section>
        </div>
      </div>
    </div>
</template>

<script>

export default {
  name: 'LevelTwo',
  data() {
    return {
      downloaded: false,
      gameInstance: null,
      containerId: 'game-container',
      timer: null,
      fin: false,
      status: '',
    }
  },
  methods:{
    async getStatus(){
      let game = await import('@/phaser-js/lvl2');
      this.fin = game.fin;
      if (this.fin){
        clearInterval(this.timer);
        this.status = game.status;
      }
    },
    toMenu(){
      this.$router.push({ name: 'game-start'});
    }
  },
  created(){
    this.timer = setInterval(
      () => this.getStatus(), 1000);
  },
  async mounted() {
    let game = await import('@/phaser-js/lvl2');
    this.downloaded = true;
    this.$nextTick(() => {
      this.gameInstance = game.launch(this.containerId);
    });
    
  },
  destroyed() {
    this.gameInstance.destroy(false)
  },
}
</script>

<style>
#no-select {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    -khtml-user-select: none; 
}
.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: #ffffff;
  box-shadow: 20px 20px 20px 20px;
  overflow-x: auto;
  display: flex;
  flex-direction: column;
}

.modal-header,
.modal-footer {
  padding: 15px;
  display: flex;
}

.modal-header {
  border-bottom: 1px solid #eeeeee;
  color: #ebac0c;
  justify-content: space-between;
}

.modal-footer {
  border-top: 1px solid #eeeeee;
  justify-content: flex-end;
}

.modal-body {
  position: relative;
  padding: 10px 10px 10px 10px;
}

.btn-close {
  border: none;
  font-size: 15px;
  cursor: pointer;
  font-weight: bold;
  color: #ebac0c;
  background: transparent;
}

.btn-func {
  color: white;
  background: #ebac0c;
  margin: 10px 20px 10px 20px;
  padding: 10px;
  border: 1px solid #ebac0c;
  border-radius: 2px;
}
</style>